.CatView {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.CatView__name {
    color: darkblue;
    position: absolute;
    left: 10px;
    top: 0;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
}

.CatView__model {
    height: 100%;
    align-self: flex-start;
    padding: 5px 0;
    transition: all .5s ease;
}

.CatView__model--eyes {
    transform: scale(2.5) translate(-50px, 200px);
    transition: all .5s ease-out;
}

.CatView__model--necklace {
    transform: scale(2.5) translate(-50px, 50px);
    transition: all .5s ease-out;
}

.CatView__model--hat {
    transform: scale(2.5) translate(-50px, 350px);
    transition: all .5s ease-out;
}
.AttributeItem {
    width: 100%;
    margin: 8px 5px;
    background: #ACACAC;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.AttributeItem:hover {
    cursor: pointer;
}

.AttributeItem--selected {
    background: #FFFFFF;
}

.AttributeItem__image {
    max-width: none;
    width: 100%;
}
.AttributeSelection {
    width: 100%;
    display: flex;
}
.AttributeColorSection {
    display: flex;
    width: 100%;
    flex-direction: column;
    margin: 8px 0;
}

.AttributeColorSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.twitter-picker {
    width: 100% !important;
}

.twitter-picker div span div {
    border: 2px solid #808183 !important;
 }
.AttributeTypeItem {
    width: 100px;
    height: 100px;
    border: 2px solid #808183;
    border-radius: 10px;
    margin: 2px;
    padding: 0;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.AttributeTypeItem:hover {
    cursor: pointer;
}

.AttributeTypeItem__check {
    display: inline;
    z-index: 1;
}

.AttributeTypeItem__check--remove {
    display: none;
}

.AttributeTypeItem__image {
    max-width: none;
    width: 100%;
    position: absolute;
}

.AttributeTypeSection {
    display: flex;
    flex-direction: column;
}

.AttributeTypeSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.AttributeTypeSection__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.AttributeOptions {
    display: flex;
    padding: 20px 30px;
    flex-direction: column;
    border-radius: 20px;
    background: #FFFFFF;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
    margin: 10px 0;    
}

.AttributeOptions__title {
    font-family: 'Poppins', sans-serif;
    font-size: 1.5rem;
    color: #808183;
    text-align: center;
    margin: 5px 0;
}
.EditPanel {
    width: 100%;
    max-width: 900px;
    z-index: 1;
}
.Creator {
    display: flex;
    width: 100%;
    height: 100%;
    background-color: #9FD1FF;
    position: relative;
}

.Creator__empty {
    width: 100%;
    height: 100%;
}
.Intro {
    background-color: rgb(28, 28, 39);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Intro__title {
    margin: 0;
    font-size: 6rem;
    color: white;
    font-family: 'Lobster', cursive;
}

.Intro__container {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    flex-direction: column;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Intro__controls {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
}

.Intro__btn {
    background-color: #4ABCAD;
    padding: 10px 15px;
    border-radius: 10px;
    text-decoration: inherit;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 1.5rem;
    margin: 10px 0;
}

.Intro__btn--gray {
    background-color: #808183;
}
html {
    height: 100%;
}

body {
    margin: 0;
    height: 100%;
}

#root {
    height: 100%;
}

.App {
    height: 100%;
}
